import React, { useState } from 'react';

import ToDo from '../ToDo/ToDo';
import TodoListBottom from './TodoListBottom';

import './ToDoList.scss';

const ToDoList = () => {
  const [todos, setTodos] = useState([
    {title: ' ', selected: false, date: new Date('2023-07-07'), desc: '    '},
    {title: ' ', selected: false, date: new Date('2023-07-08'), desc: '    '},
    {title: '   ', selected: false, date: new Date('2023-07-09'), desc: '     '}
  ])

  const handleDelete = (title) => {
    setTodos(todos.filter(todo => todo.title !== title));
  };

  const deleteSelected = () => {
    setTodos(prevTodos => prevTodos.filter(todo => !todo.selected));
  };  

  const handleSelect = (title) => {
    setTodos(prevTodos => {
      return prevTodos.map(todo => {
        if (todo.title === title) {
          return { ...todo, selected: !todo.selected };
        }
        return todo;
      });
    });
  };


  const addTodo = (todo) => {
    let newTodos = [...todos, todo];

    for (let i = 0; i < newTodos.length; i++) {
      for (let j = 0; j < newTodos.length -1; j++) {
        if (newTodos[j].date > newTodos[j + 1].date) {
          const current = newTodos[j];
          const next = newTodos[j + 1];
  
          newTodos[j] = next;
          newTodos[j + 1] = current;
        }
      }
    }
  
    setTodos(newTodos);
  };
  

  return (
    <div className='ToDoList'>
      <div className="ToDoList__items">
        {todos.map((todo, index) => (
          <ToDo
            key={todo.title}
            title={todo.title}
            date={todo.date}
            author={todo.author}
            desc={todo.desc}
            onDelete={handleDelete}
            onSelect={handleSelect}
          />      
        ))}
      </div>
      <TodoListBottom 
        deleteSelected={deleteSelected}
        addTodo={addTodo}
      />
    </div>
  );
};

const nums = [199, 196, 196, 192, 189, 188, 188, 187, 186, 183, 183, 183, 181, 181, 180, 177, 176, 176, 176, 171, 171, 170, 168, 166, 165, 165, 164, 162, 161, 160, 160, 158, 158, 155, 153, 153, 150, 149, 146, 145, 144, 142, 142, 138, 138, 137, 133, 133, 132, 130, 130, 125, 120, 115, 104]

const sum = nums.reduce((acc, num) => acc + num, 0);
const average = sum / nums.length;

console.log(` : ${nums.length}`);
console.log(`: ${Math.min(...nums)}`);
console.log(`: ${Math.max(...nums)}`);
console.log(` : ${average.toFixed(2)}`);

const index = nums.indexOf(149);
console.log(`  148: ${index}`);

export default ToDoList;
